import * as actionType from './actionType'
import { getData } from 'util'
import { API_TRANSFERRECORDS_PAGE, API_TRANSFERRECORD_DET_LIST } from 'api'
//同步
export const update = object => {
  console.log('object is ', object)
  return {
    type: actionType.TRANSFERRECORD_UPDATE,
    data: object
  }
}

//异步
// 获取列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.TRANSFERRECORD_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_TRANSFERRECORDS_PAGE, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page, size } = reqData.searchParams
      dispatch({
        type: actionType.TRANSFERRECORD_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          searchResult: data.result.list || []
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.TRANSFERRECORD_FAIL
      })
    })
}
// 获得某个调拨击落的明细list
export const getTransferRecordListDet = (reqData, callBack) => dispatch => {
  getData({ api: API_TRANSFERRECORD_DET_LIST, ...reqData })
    .then(data => {
      callBack && callBack(data)
    })
    .catch(err => {
      dispatch({
        type: actionType.PRODUCTORDERS_FAIL
      })
    })
}



