import React, { PureComponent } from 'react'
import { Form, Button, Row, Col, Input, DatePicker } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { CsxSelect } from 'Form'
import {
  API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_LIST
 } from 'api'
const { RangePicker } = DatePicker
import moment from 'moment'
import { transferStatusOptions, transferTypeOptions } from 'enum'
@connect(
  state => ({ ...state.transferRecord }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }

  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  // 查询数据
  query = () => {
    const { handleGetList, searchParams, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    delete reqObj.initTime
    delete reqObj.initProduceTime
    handleGetList({ searchParams: { ...reqObj, page: 1, size } })
  }

  // 重置
  reset = () => {
    const { update } = this.props
    let nowDate = moment()
    let transferTimeStart = `${nowDate.format('YYYY-MM-DD ')} 00:00:00`,
      transferTimeEnd = `${nowDate.format('YYYY-MM-DD ')} 23:59:59`
    update({ searchParams: {
      initTime: [nowDate, nowDate],
      transferTimeStart,
      transferTimeEnd
    }, productCodeOptions: {} })
  }
  render() {
    const { searchParams = {}, menuPermissionIds = [] } = this.props
    const {
      initTime,
      productLineCode,
      initProduceTime,
      transferType,
      ownTransferCode,
      transferCode,
      transferStatus
    } = searchParams
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
          <Col span={8}>
            <CsxSelect
              data={transferTypeOptions}
              label='调拨类型'
              allowClear={true}
              selectItem={{ data: transferType, key: 'key', valueKey: 'value' }}
              onChangeSelect={value => {
                this.onInputChange({ transferType: value })
              }}
            />
          </Col>
          <Col span={8}>
            <div className='commonInput '>
              <label htmlFor=''>调拨单号：</label>
              <div className='box'>
                <Input
                  value={ownTransferCode}
                  onChange={e => {
                    const {
                      target: { value = '' }
                    } = e
                    this.onInputChange({ ownTransferCode: value })
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className='commonInput '>
              <label htmlFor=''>关联单号：</label>
              <div className='box'>
                <Input
                  value={transferCode}
                  onChange={e => {
                    const {
                      target: { value = '' }
                    } = e
                    this.onInputChange({ transferCode: value })
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <CsxSelect
              data={transferStatusOptions}
              label='调拨状态'
              allowClear={true}
              selectItem={{
                data: transferStatus,
                key: 'key',
                valueKey: 'value'
              }}
              onChangeSelect={value => {
                this.onInputChange({ transferStatus: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_LIST}
              label='产线编码'
              allowClear={true}
              selectItem={{
                data: productLineCode,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={value => {
                this.onInputChange({ productLineCode: value })
              }}
            />
          </Col>
          <Col span={8}>
            <div className={'commonInput showTimePicker showTimePickerNotSecond'}>
              <label>生产日期：</label>
              <div className='box ' style={{ marginRight: 0 }}>
                <RangePicker
                  allowClear={true}
                  // showTime
                  value={initProduceTime}
                  onChange={(time, string) => {
                    let orderStartTime =
                      time &&
                      time[0] &&
                      `${moment(time[0]).format('YYYY-MM-DD')} 00:00:00`
                    let orderEndTime =
                      time &&
                      time[1] &&
                      `${moment(time[1]).format('YYYY-MM-DD')} 23:59:59`
                    this.onInputChange({
                      orderStartTime,
                      orderEndTime,
                      initProduceTime: time
                    })
                  }}
                  format='YYYY-MM-DD'
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className={'commonInput showTimePicker showTimePickerNotSecond'}>
              <label>操作时间：</label>
              <div className='box ' style={{ marginRight: 0 }}>
                <RangePicker
                  allowClear={true}
                  // showTime
                  value={initTime}
                  onChange={(time, string) => {
                    let transferTimeStart =
                      time &&
                      time[0] &&
                      `${moment(time[0]).format('YYYY-MM-DD')} 00:00:00`
                    let transferTimeEnd =
                      time &&
                      time[1] &&
                      `${moment(time[1]).format('YYYY-MM-DD')} 23:59:59`
                    this.onInputChange({
                      transferTimeStart,
                      transferTimeEnd,
                      initTime: time
                    })
                  }}
                  format='YYYY-MM-DD'
                />
              </div>
            </div>
          </Col>

          <Col span={8} push={8}>
            <Row type='flex' justify='end'>

              {
              menuPermissionIds && menuPermissionIds.includes('transferRecord_find') &&
              <Button
              type='primary'
              className={style.mr10}
              onClick={this.query}
            >
              查询
            </Button>
            }
            <Button type='primary' onClick={this.reset}>
                重置
              </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



