import React, { PureComponent } from 'react'
import { Table, Tooltip } from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
import { transferStatus, transferTypeList } from 'enum'
import style from './style/Table.scss'
import moment from 'moment'
@connect(
  state => ({ ...state.transferRecord, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
    this.columns = [
      {
        title: '调拨类型',
        width: 80,
        dataIndex: 'transferType',
        key: 'transferType',
        render: text => transferTypeList[text] || '-'
      },
      {
        title: '调拨单号',
        width: 120,
        dataIndex: 'ownTransferCode',
        key: 'ownTransferCode'
      },
      {
        title: '关联单号',
        width: 130,
        dataIndex: 'transferCode',
        key: 'transferCode'
      },
      {
        title: '调出',
        width: 60,
        dataIndex: 'transferOut',
        key: 'transferOut'
      },
      {
        title: '调入',
        width: 60,
        dataIndex: 'transferIn',
        key: 'transferIn'
      },
      {
        title: '调拨状态',
        width: 80,
        dataIndex: 'transferStatus',
        key: 'transferStatus',
        render: text => transferStatus[text] || '--'
      },
      {
        title: '操作时间',
        width: 140,
        dataIndex: 'transferTime',
        key: 'transferTime'
      },
      {
        title: '操作人',
        width: 60,
        dataIndex: 'operatedBy',
        key: 'operatedBy'
      },
      {
        title: '生产日期',
        width: 100,
        dataIndex: 'produceDate',
        key: 'produceDate'
      },
      {
        title: '产线',
        width: 120,
        dataIndex: 'produceLineCode',
        key: 'produceLineCode',
        render: (text, record) => {
          const { produceLineName = '', produceLineCode = '' } = record
          return (
            `${produceLineCode} ${produceLineName}`
          )
        }
      },
      {
        title: '操作',
        width: 90,
        dataIndex: 'xx',
        key: 'xx',
        render: (text, record) => {
          const columnsMaterial = this.renderTransferDetList() // 物料需求量
          return (
            <Tooltip
            placement='right'
            overlayStyle={{ maxWidth: '750px', minWidth: '450px' }}
            title={columnsMaterial}
            trigger='click'
            overlayClassName={'tootip_in_table'}
            onVisibleChange={this.onVisibleChange.bind(this, record)}
          >
            <span className={style.btn_ib_table}>查看明细</span>
          </Tooltip>
          )
        }
      }
    ]
  }
  componentDidMount() {
    const { handleGetList, searchParams, page, size, update } = this.props
    let nowDate = moment()
    let transferTimeStart = `${nowDate.format('YYYY-MM-DD ')} 00:00:00`,
      transferTimeEnd = `${nowDate.format('YYYY-MM-DD ')} 23:59:59`
    update({
      searchParams: {
        initTime: [nowDate, nowDate],
        transferTimeStart,
        transferTimeEnd
      }
    })
    handleGetList({ searchParams: { page, size, ...searchParams, transferTimeStart, transferTimeEnd } })
  }
  // 查看明细 - 调拨明细
  onVisibleChange = (record, bool) => {
    const { getTransferRecordListDet } = this.props
    const { id = '' } = record
    if (bool) {
      getTransferRecordListDet(
        {
          searchParams: { transferCode: id }
        },
        data => {
          const { result = [] } = data
          const formatterData = [...result]
          this.setState({
            transferCodeList: formatterData
          })
        }
      )
    } else {
      this.setState({
        transferCodeList: []
      })
    }
  }
  // 物料需求量
  renderTransferDetList = () => {
    const { transferCodeList = [] } = this.state
    return (
      <div className={`${style.tr_in_table} ${style.table_scroll}` }>
        <table>
          <thead>
            <tr>
              <th className={`${style.td_in_table_one} ${style.th_3}`}>工单编号</th>
              <th className={style.td_in_table_one}>商品编码</th>
              <th className={style.td_in_table_one}>商品名称</th>
              <th className={style.td_in_table_one}>规格</th>
              <th className={style.td_in_table_one}>单位</th>
              <th className={style.td_in_table_one}>调出</th>
              <th className={style.td_in_table_one}>调入</th>
              <th className={style.td_in_table_one}>调拨数量</th>
              <th className={style.td_in_table_one}>备注</th>
            </tr>
          </thead>
          <tbody>
            {transferCodeList &&
              transferCodeList.length > 0 &&
              transferCodeList.map((item, i, arr) => {
                const {
                  orderCode = '',
                  productCode = '',
                  productName = '',
                  productUnit = '',
                  transferOut = '',
                  transferIn = '',
                  quantity = '',
                  // realReceiveQty = '',
                  spec = '',
                  remark = '',
                  id = ''
                } = item
                return (
                  <tr key={id}>
                    <td>{orderCode}</td>
                    <td>{productCode}</td>
                    <td>{productName}</td>
                    <td>{spec}</td>
                    <td>{productUnit}</td>
                    <td>{transferOut}</td>
                    <td>{transferIn}</td>
                    <td>{quantity}</td>
                    <td>{remark}</td>
                  </tr>
                )
              })}
          </tbody>
        </table>
      </div>
    )
  }
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, userLoation } = this.props
    const newUserLoationValue = newProps && newProps.userLoation && newProps.userLoation.value || ''
    const userLoationValue = userLoation && userLoation.value || ''
    if (newUserLoationValue != userLoationValue) {
      handleGetList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams } })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      delete reqObj.initTime
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  render() {
    const { total, page, size, tableLoading, searchResult = [] } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['10', '50', '100', '200', '500']
    }

    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.transferCode + record.productCode}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{ x: countTableScroll(this.columns) }}
          onChange={this.onChange}
          pagination={pagination}
        />
      </div>
    )
  }
}



